<template>
  <q-page class="bg-white">
    <q-card flat class="container bg-trasnparent">
      <q-card flat>
        <div class="row q-col-gutter-md items-center q-pb-lg">
          <div
            class="col-12 col-sm-auto"
            :style="`min-width: ${splitterModel + 35}px;`"
          >
            <div class="flex flex-start">
              <q-avatar
                square
                color="accent"
                size="136px"
                class="rounded"
                text-color="secondary font-medium"
              >
                <span v-if="brandInfo.label">
                  {{ avatarNameAbbr(brandInfo.label) }}
                </span>
                <q-icon v-else name="icon-star-asterisk" size="60px" />
              </q-avatar>
            </div>
          </div>
          <div class="col-12 col-sm-auto">
            <q-breadcrumbs
              active-color="primary"
              class="text-primary"
              gutter="xs"
            >
              <template v-slot:separator>
                <q-icon
                  size="xs"
                  name="icon-chevron-right"
                  class="text-grey-6 q-py-xs"
                />
              </template>
              <q-breadcrumbs-el
                icon="icon-home-line"
                class="q-py-xs q-pr-sm cursor-pointer hover-text-secondary"
                @click="$router.push('/')"
              />
              <q-breadcrumbs-el
                label="Workspace"
                class="text-body2 q-py-xs q-px-sm rounded cursor-pointer hover-text-secondary color-primary2 font-medium"
                @click="$router.push('/workspace')"
              />
              <q-breadcrumbs-el
                label="Brand Overview"
                class="text-body2 q-py-xs q-px-sm rounded cursor-pointer hover-text-secondary color-primary2 font-medium"
                @click="$router.push(`/workspace/${brandGID}`)"
              />
              <q-breadcrumbs-el
                label="Task Overview"
                class="text-body2 text-secondary bg-accent q-py-xs q-px-sm rounded font-medium"
              />
            </q-breadcrumbs>
            <div class="q-pt-md q-pb-sm" id="overview-title">
              <div class="row items-center q-gutter-sm">
                <span class="text-h4 font-medium text-primary">
                  {{ taskInfo.name }}
                </span>
                <q-badge
                  class="badge badge-blue font-medium cursor-pointer"
                  @click="$router.push(`/workspace/${brandGID}`)"
                >
                  {{ brandInfo.label }}
                </q-badge>
              </div>
            </div>
          </div>
        </div>
      </q-card>

      <!-- Body -->

      <q-card flat>
        <q-splitter
          v-model="splitterModel"
          unit="px"
          id="mainSplitter"
          ref="mainSplitter"
          :separator-class="`bg-transparent q-mx-md${
            $q.screen.lt.sm ? ' hidden' : ''
          }`"
          :before-class="`sticky sticky-menu-top${
            $q.screen.lt.sm ? ' hidden' : ''
          }`"
        >
          <template v-slot:before>
            <div class="q-gutter-sm">
              <q-card
                flat
                v-ripple="{ color: 'secondary' }"
                v-for="(tab, i) in allSections"
                :key="tab.key"
                class="cursor-pointer rounded hover-text-secondary hover-bg-accent text-body2 font-medium"
                :class="
                  (tab.key === tabSectionId && tabSectionIdScrolled === '') ||
                  tabSectionIdScrolled === tab.key ||
                  (tabSectionIdScrolled === '' &&
                    tabSectionId === '' &&
                    i === 0)
                    ? 'bg-accent text-secondary'
                    : 'color-primary2'
                "
                @click="clickScrollEvent(tab.key, false)"
              >
                <q-btn
                  flat
                  no-caps
                  :ripple="false"
                  class="rounded hover-text-secondary hover-bg-accent"
                >
                  <div class="full-width text-left">{{ tab.label }}</div>
                </q-btn>
              </q-card>
            </div>
          </template>

          <template v-slot:after>
            <q-card flat class="text-body2 q-pb-xl">
              <q-card-section class="q-pa-none font-medium text-h5">
                <div class="row justify-end items-center">
                  Task Overview

                  <q-space />

                  <div v-if="!editing" class="q-gutter-sm">
                    <q-btn
                      v-if="documentReady"
                      flat
                      round
                      size="sm"
                      icon="icon-printer"
                      class="color-primary2 hover-text-secondary"
                      :disable="!documentReady || convertingPDF"
                      @click="exportToPDF"
                    >
                      <tooltip-pro
                        :anchor="`${
                          $q.screen.lt.md ? 'center left' : 'bottom middle'
                        }`"
                        :self="`${
                          $q.screen.lt.md ? 'center right' : 'top middle'
                        }`"
                      >
                        Export to PDF
                      </tooltip-pro>
                    </q-btn>
                    <q-btn
                      flat
                      round
                      size="sm"
                      icon="icon-edit-05"
                      class="color-primary2 hover-text-secondary"
                      @click="editing = true"
                    >
                      <tooltip-pro>Edit</tooltip-pro>
                    </q-btn>
                  </div>
                  <div v-else class="q-gutter-sm">
                    <q-btn
                      no-caps
                      outline
                      size="sm"
                      class="rounded not-gm hover-negative"
                      @click.stop="editing = false"
                    >
                      Cancel
                    </q-btn>
                    <q-btn
                      no-caps
                      unelevated
                      size="sm"
                      color="primary"
                      class="rounded not-gm hover-bg-secondary"
                      @click.stop="editing = false"
                    >
                      <q-icon name="icon-save-01" size="xs" />
                      <span class="q-pl-xs">Save Changes</span>
                    </q-btn>
                  </div>
                </div>
              </q-card-section>
              <q-card-section
                id="description"
                class="q-px-none hover-visibility"
              >
                <div
                  class="q-pb-md font-medium text-subtitle1 target-clickable-id"
                  @click="
                    () =>
                      noTitleClick ? void 0 : clickScrollEvent('description')
                  "
                >
                  Description
                </div>
                <div v-if="!editing">
                  <div class="raw-html" v-html="overviewData.description"></div>
                </div>
                <div v-else>
                  <q-editor
                    ref="descriptionEditorRef"
                    @paste="
                      (e) => pastePlainText(e, $refs['descriptionEditorRef'])
                    "
                    v-model="overviewData.description"
                    placeholder="Enter a description..."
                    height="250px"
                    :toolbar="[
                      [
                        {
                          icon: $q.iconSet.editor.align,
                          fixedLabel: true,
                          list: 'only-icons',
                          options: ['left', 'center', 'right', 'justify'],
                        },
                      ],
                      [
                        {
                          label: $q.lang.editor.formatting,
                          icon: $q.iconSet.editor.formatting,
                          list: 'no-icons',
                          options: ['p', 'h5', 'h6', 'code'],
                        },
                      ],
                      ['bold', 'italic', 'underline'],
                      ['link', 'quote'],
                      ['unordered', 'ordered', 'outdent', 'indent'],
                      ['undo', 'redo'],
                    ]"
                  />
                </div>
              </q-card-section>
              <q-card-section id="target_audience" class="q-px-none">
                <div
                  class="q-pb-md font-medium text-subtitle1 target-clickable-id"
                  @click="
                    () =>
                      noTitleClick
                        ? void 0
                        : clickScrollEvent('target_audience')
                  "
                >
                  Target Audience
                </div>
                <div v-if="!editing">
                  <template
                    v-for="sentence in overviewData.target_audience.split('\n')"
                    :key="sentence"
                  >
                    <p>
                      {{ sentence }}
                    </p>
                  </template>
                </div>
                <div v-else>
                  <q-input
                    dense
                    outlined
                    type="textarea"
                    class="rounded"
                    v-model="overviewData.target_audience"
                  />
                </div>
              </q-card-section>

              <q-card-section id="video_walkthrough" class="q-px-none">
                <div
                  class="q-pb-md font-medium text-subtitle1 target-clickable-id"
                  @click="
                    () =>
                      noTitleClick
                        ? void 0
                        : clickScrollEvent('video_walkthrough')
                  "
                >
                  Video Walkthrough
                </div>

                <div v-if="!editing">
                  <q-video
                    :ratio="16 / 9"
                    class="rounded fit"
                    title="Video Walkthrough"
                    :src="overviewData.video_walkthrough"
                  />
                </div>
                <div v-else>
                  <q-input
                    dense
                    outlined
                    class="rounded"
                    v-model="overviewData.video_walkthrough"
                  />
                </div>
              </q-card-section>
              <q-card-section id="warning" class="q-px-none q-pb-md">
                <q-card flat class="rounded bg-color q-pa-md">
                  <div
                    class="target-clickable-id full-width"
                    @click="
                      () =>
                        noTitleClick ? void 0 : clickScrollEvent('warning')
                    "
                  >
                    <div
                      class="items-center flex text-negative font-medium q-gutter-x-sm"
                    >
                      <q-icon name="icon-alert-circle" class="font-medium" />
                      <span>Warning</span>
                    </div>
                  </div>
                  <div v-if="!editing" class="q-pt-sm color-primary2">
                    {{ overviewData.warning }}
                  </div>
                  <div v-else class="q-pt-sm">
                    <q-input
                      dense
                      outlined
                      type="textarea"
                      class="rounded"
                      v-model="overviewData.warning"
                    />
                  </div>
                </q-card>
              </q-card-section>
              <q-card-section id="resources" class="q-px-none">
                <div
                  class="q-pb-md font-medium text-subtitle1 target-clickable-id"
                  @click="
                    () =>
                      noTitleClick ? void 0 : clickScrollEvent('resources')
                  "
                >
                  Resources
                </div>
                <template v-if="!editing">
                  <div
                    id="resources-images"
                    class="row items-center justify-start q-col-gutter-md"
                  >
                    <div
                      v-for="i in 4"
                      :key="i"
                      :ratio="1"
                      class="rounded overflow-hidden col-12 col-sm-6"
                    >
                      <q-img
                        :src="`${$cdn_host}${$cdn.tmp}/Image-${i}.png`"
                        class="rounded fit with-lightbox"
                        spinner-color="secondary"
                      />
                    </div>
                  </div>
                  <div id="resources-content" class="q-pt-md">
                    {{ overviewData.resources_text }}
                    <ul>
                      <li
                        v-for="(link, i) in overviewData.resources_links"
                        :key="i"
                        class="text-secondary"
                      >
                        <external-link :url="link" color="text-secondary" />
                      </li>
                    </ul>
                  </div>
                </template>
                <div v-else>
                  <FilesUploader
                    multiple
                    ref="upResouces"
                    max-file-size="10000000"
                    note="AI, EPS, PSD, SVG, PNG, GIF or JPEG (max. 10MB)"
                    :filter="
                      (files) =>
                        checkFileExtensions(files, [
                          '.png',
                          '.ico',
                          '.jpg',
                          '.jpeg',
                          '.svg',
                          '.ai',
                          '.psd',
                          '.eps',
                        ])
                    "
                    @rejected="onRejected"
                    :removedFile="overviewData.resources_files?.removedFile"
                    @added="
                      (files) =>
                        addUploadToModel(
                          'overviewData',
                          'resources_files',
                          files,
                          'upResouces'
                        )
                    "
                  />
                  <div
                    v-if="overviewData.resources_files?.files?.length"
                    class="full-width scroll q-mt-md"
                    style="max-height: 175px"
                  >
                    <UploadSlot
                      :small="$q.screen.lt.md"
                      thumbnail="icon-image-03"
                      :scope="overviewData.resources_files"
                    />
                    <div
                      v-if="overviewData.resources_files?.files?.length > 2"
                      class="absolute-bottom bg-gradient no-pointer-events sticky"
                      style="height: 50px"
                    ></div>
                  </div>
                  <div class="q-pt-md">
                    <q-input
                      dense
                      outlined
                      type="textarea"
                      class="rounded"
                      v-model="overviewData.resources_text"
                    />
                  </div>
                </div>
              </q-card-section>
            </q-card>
          </template>
        </q-splitter>
      </q-card>
    </q-card>
    <q-scroll-observer @scroll="scrollHandler" />
    <image-lightbox
      :visible="showLightBox"
      :imgs="lightboxImages"
      :index="lightboxIndex"
      @hide="showLightBox = false"
    />
  </q-page>
</template>

<script>
import { defineComponent } from 'vue';
import { mapState } from 'pinia';
import useUser from 'stores/user';
import { copyToClipboard, date, debounce, format, LoadingBar } from 'quasar';
import {
  avatarNameAbbr,
  getImageStringFromURL,
} from 'src/assets/scripts/functions';
const { capitalize } = format,
  getEl = (id) => {
    return document.getElementById(id);
  };
import html2pdf from 'html2pdf.js';
import FilesUploader from 'src/components/Helpers/FilesUploader.vue';
import UploadSlot from 'src/components/Helpers/Slots/UploadSlot.vue';
import { FileUploader_Mixin, Editor_Mixin } from 'src/mixins';

export default defineComponent({
  name: 'TaskOverview',

  mixins: [FileUploader_Mixin, Editor_Mixin],

  components: { FilesUploader, UploadSlot },

  data() {
    return {
      title: 'Task Overview',
      showLightBox: false,
      lightboxIndex: 0,
      lightboxImages: [],
      tabSectionId: '',
      tabSectionIdScrolled: '',
      splitterModel: 175,
      editing: false,
      noTitleClick: false,
      documentReady: false,
      capitalize,
      avatarNameAbbr,
      allSections: [
        { label: 'Description', key: 'description' },
        { label: 'Target Audience', key: 'target_audience' },
        { label: 'Video Walkthrough', key: 'video_walkthrough' },
        { label: 'Warning', key: 'warning' },
        { label: 'Resources', key: 'resources' },
      ],
      overviewData: {
        description:
          '<div>Dolor enim eu tortor urna sed duis nulla. Aliquam vestibulum, nulla odio nisl vitae. In aliquet pellentesque aenean hac vestibulum turpis mi bibendum diam. Tempor integer aliquam in vitae malesuada fringilla. Elit nisi in eleifend sed nisi. Pulvinar at orci, proin imperdiet commodo consectetur convallis risus.</div><div><br/></div><div>Dolor enim eu tortor urna sed duis nulla. Aliquam vestibulum, nulla odio nisl vitae. In aliquet pellentesque aenean hac vestibulum turpis mi bibendum diam. Tempor integer aliquam in vitae malesuada fringilla.</div><div><br/></div><div>Elit nisi in eleifend sed nisi. Pulvinar at orci, proin imperdiet commodo consectetur convallis risus. Sed condimentum enim dignissim adipiscing faucibus consequat, urna. Viverra purus et erat auctor aliquam. Risus, volutpat vulputate posuere purus sit congue convallis aliquet. Arcu id augue ut feugiat donec porttitor neque. Mauris, neque ultricies eu vestibulum, bibendum quam lorem id. Dolor lacus, eget nunc lectus in tellus, pharetra, porttitor.</div>',
        target_audience:
          'Ipsum sit mattis nulla quam nulla. Gravida id gravida ac enim mauris id.Diam elit, orci, tincidunt aenean tempus. Quis velit eget ut tortor tellus. Sed vel, congue felis elit erat nam nibh orci. Non pellentesque congue eget consectetur turpis. Sapien, dictum molestie sem tempor. Diam elit, orci, tincidunt aenean tempus. Quis velit eget ut tortor tellus. Sed vel, congue felis elit erat nam nibh orci.\nDolor enim eu tortor urna sed duis nulla. Aliquam vestibulum, nulla odio nisl vitae. In aliquet pellentesque aenean hac vestibulum turpis mi bibendum diam. Tempor integer aliquam in vitae malesuada fringilla.',
        video_walkthrough:
          'https://www.loom.com/embed/c2744e314e2b43f0bcad53c96dfb9741',
        warning:
          'Lorem ipsum dolor sit, amet consectetur adipisicing elit. Quis praesentium cumque magnam odio iure quidem, quod illum numquam possimus obcaecati commodi minima assumenda consectetur culpa fuga nulla ullam. In, libero.',
        resources_text:
          'Dolor enim eu tortor urna sed duis nulla. Aliquam vestibulum, nulla odio nisl vitae. In aliquet pellentesque aenean hac vestibulum turpis mi bibendum diam. Tempor integer aliquam in vitae malesuada fringilla.',
        resources_links: ['drive.google.com', 'loom.com'],
        resources_files: null,
      },
      scrollHandler: debounce(this.scrollHandlerOrig, 100),
      convertingPDF: false,
    };
  },

  mounted() {
    this.$root.setMeta({ title: this.title });
    this.tabSectionId = this.hashId;
    this.updateLightBox();
    this.$nextTick(() => {
      this.documentReady = true;
    });
  },

  computed: {
    ...mapState(useUser, ['selectedOrgBrands', 'selectedOrgTasks']),

    hashId() {
      return this.$route.hash.slice(1);
    },

    brandGID() {
      return this.$route.params?.brandGID;
    },

    taskId() {
      return this.$route.params?.taskId;
    },

    brandInfo() {
      return this.selectedOrgBrands[this.brandGID] || {};
    },

    taskInfo() {
      return this.selectedOrgTasks[this.brandGID]?.[this.taskId] || {};
    },
  },

  watch: {
    editing: {
      async handler(val) {
        setTimeout(this.updateLightBox, 500);
      },
    },
    tabSectionId: {
      async handler(val) {
        if (val) {
          // this.$router.push({ hash: '#' + val });
          this.scrollToTarget(val);
          this.tabSectionId = '';
          // this.tabSectionIdScrolled = '';
        }
      },
    },
    convertingPDF: {
      async handler(val) {
        if (val) LoadingBar.start();
        else LoadingBar.stop();
      },
    },
  },

  methods: {
    updateLightBox() {
      const lightboxElems = document.querySelectorAll('.with-lightbox');
      lightboxElems.forEach((e, i) => {
        let img = e.querySelector('img');
        this.lightboxImages.push({
          src: img.src,
          title: img.title,
          alt: img.alt,
        });
        e.onclick = () => {
          this.lightboxIndex = i;
          this.showLightBox = true;
        };
      });
    },

    scrollToTarget(targetId) {
      getEl(targetId)?.scrollIntoView?.({
        block: 'start',
        inline: 'nearest',
        behavior: 'smooth',
      });
    },

    clickScrollEvent(targetId, copy = true) {
      this.tabSectionId = targetId;
      // Copy URL to Clipboard
      if (copy) {
        copyToClipboard(
          window.location.origin + this.$route.fullPath + '#' + targetId
        );
        this.$q.notify({
          icon: 'icon-check-circle-broken',
          message: 'Anchor has been copied to clipboard.',
          timeout: 500,
        });
      }
    },

    scrollHandlerOrig(scroll) {
      this.tabSectionIdScrolled = '';
      let selectedKey = '';
      this.allSections
        .map((e) => e.key)
        .reverse()
        .forEach((key) => {
          const el = getEl(key),
            offsetTop = el?.offsetTop + 25,
            top = scroll?.position?.top;
          if (top > offsetTop && !selectedKey) {
            selectedKey = key;
          }
        });
      this.tabSectionIdScrolled = selectedKey;
    },

    async exportToPDF() {
      this.convertingPDF = true;
      const notif = this.$q.notify({
        group: false, // required to be updatable
        spinner: true,
        progress: false,
        message: 'Generating PDF Document',
        timeout: 0,
      });
      let percentage = 10;
      const interval = setInterval(() => {
        percentage = Math.min(99, percentage + Math.floor(Math.random() * 25));
        notif({
          caption: `Processing... ${percentage}%`,
        });
        if (percentage === 99) {
          notif({
            message: 'Finalizing PDF',
            caption: undefined,
          });
          clearInterval(interval);
        }
      }, 500);
      const element = await this.generateElement4PDF();
      this.convertNow(element)
        .then((status) => {
          // Notify PDF Status
          notif({
            message: status ? 'PDF Generated' : 'Failed Generating PDF',
            icon: status ? 'icon-check-circle-broken' : 'icon-alert-triangle',
            iconColor: status ? 'secondary' : 'negative',
            spinner: false,
            caption: undefined,
            timeout: 1500,
          });
        })
        .finally(() => {
          // Disable processing status
          this.convertingPDF = false;
          // Clear Interval
          clearInterval(interval);
        });
    },

    async generateElement4PDF(withFooter = false) {
      const element = document.createElement('div');
      const ids = [
        'overview-title',
        'description',
        'target_audience',
        'video_walkthrough',
        'warning',
        'resources',
      ];
      for (const id of ids) {
        const el = document.getElementById(id).cloneNode(true);
        const images = el.querySelectorAll('img');
        // Replace all images with base64
        for (const e of images) {
          let newSrc = await getImageStringFromURL(e.src, false);
          e.src = newSrc;
        }
        if (id === 'video_walkthrough') {
          el.querySelector('.q-video')?.remove?.();
          let video_link = document.createElement('a');
          video_link.append(this.overviewData?.video_walkthrough);
          video_link.href = this.overviewData?.video_walkthrough;
          el.append(video_link);
        } else if (id === 'resources') {
          // el.querySelector('#resources-images').remove();
          const res_content = el.querySelector('#resources-content');
          res_content.classList.remove('q-pt-md');
        }
        element.append(el);
      }

      // Footer Section
      if (withFooter) {
        const footer = document.createElement('div');
        footer.classList.add(
          'full-width',
          'text-center',
          'text-caption',
          'color-body2',
          'font-thin',
          'letter-space-normal',
          'q-pt-xl'
        );
        footer.append(
          '© ' +
            date.formatDate(
              this.$root.realtimeDate || Date.now(),
              'MMMM DD, YYYY - HH:mm:ss'
            )
        );
        element.append(footer);
      }
      return element;
    },

    async convertNow(element) {
      let filename =
        `TASK-${this.taskInfo?.name}-${this.brandInfo?.label}.pdf`.replaceAll(
          ' ',
          '_'
        );
      return await html2pdf()
        .set({ filename, ...this.$config.default?.html2pdf })
        .from(element)
        .to('pdf')
        .save()
        .then(() => {
          return true;
        })
        .catch(() => {
          return false;
        });
    },
  },
});
</script>
